@use '/src/assets/css/variables' as *;

header {
    width: 100%;
    height: $header_height;
    background-color: var(--main);
    transition: background-color $transition-theme;
    position: sticky;
    top: 0;
    z-index: 5000;
    border-bottom: 2px solid var(--main_vv);
    //box-shadow: 0px 3px 3px var(--main);
    
    & .header__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;
        background-color: var(--main);
        transition: $transition_theme;


        & .logo-link {
            height: 100%;
            display: flex;
            align-items: center;
    
            & > img {
                height: 90%;
            }
        }
    }

}